<template>
    <div class="backgroundImg">
        <div class="setMain" v-if="Wsetpage=='main'">
            <h1 class="setMainTex"><b>[2024 2학기] 1학년<br>단어선택 리스트</b></h1>
            <p class="setText">세트</p>
            <div class="setListItem" v-for="file in files">
                <div class="setListWord">
                    <h3 class="setListWordText"><b>단어</b></h3>
                </div>
                <h3 class="setListText" @click="changePageWords(file.name)">{{ file.name }}</h3>
            </div>
        </div>
        <div class="setWords" v-if="Wsetpage=='words'">
            <div class="upperBg">
                <div class="wordDiv">
                    <p class="wordDivText">단어</p>
                </div>
                <p class="fileNameText"><b>{{ this.fileNameFor }}</b></p>
            </div>
            <div class="middleBg">

            </div>
            <div class="wordMean" v-for="word in fileForWord">
                <p class="wordMeanText">
                    ◼
                    <span class="wordMeanTextWord">
                        {{ word.word }}
                    </span>
                    <br>
                    <div class="wordMeanTextDiv" style="width:fit-content">
                        <p class="wordMeanTextMeaning" style="display:inline-block">
                            {{ word.meaning }}
                        </p>
                    </div>
                </p>
            </div>
            <div class="wordMenuBar">
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="toAm">암기</h2>
                </div>
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="Wsetpage='word2'">리콜</h2>
                </div>
                <div class="wordMenuButton">
                    <h2 class="wordMenuButtonText" @click="Wsetpage='word3'">테스트</h2>
                </div>
            </div>
        </div>
        <div class="word1Page" v-if="Wsetpage=='word1'">
        <!-- !This is the first word page -->
            <div class="word1PageMiddle">
            </div>
            <p class="quiz1Word">{{ fileForWord[this.probCount].word }}</p>
            <div class="word1PageProb" @click="changeWordProbT2(0)">
                <p class="word1PageProbText">{{ this.arr[0] }}</p>
            </div>
            <div class="word1PageProb" @click="changeWordProbT2(1)">
                <p class="word1PageProbText">{{ this.arr[1] }}</p>
            </div>
            <div class="word1PageProb" @click="changeWordProbT2(2)">
                <p class="word1PageProbText">{{ this.arr[2] }}</p>
            </div>
        </div>
        <div class="word2Page" v-if="Wsetpage=='word2'">
        <!-- !This is the second word page -->
            <div class="middleDivForW2">
                단어가 들어갑니다
            </div>
            <div class="InputTools">
                <input class="InputField" placeholder="정답을 입력하세요!" type="text">
                <button class="InputButton">←</button>                
            </div>
        </div>
        <div class="word3Page" v-if="Wsetpage=='word3'">
        <!-- !This is the finall word page - chat page. -->

        </div>
    </div>
</template>
<script>
import fileList from '@/assets/words/files.json';
import firJSON from '@/assets/words/firstWords.json';
import Swal from 'sweetalert2';

const files = fileList;
const firstJSON = firJSON;
let fileForWord = firstJSON;
let fileNameFor = '중1 비상 테스트 단원';
let randNum;
let wordList = new Array();
let wordMeanings, wordWords = new Array();
let arr = new Array();
let probCount = fileForWord.length-1;
let clickCount = 0;
let correctAnswer = 0;
const Toast = Swal.mixin({
  toast: true,
  position:'top-end',
  showConfirmButton: false,
  timer: 1500,
  timerProgressBar: true,
  /* didOpen: (toast) => {
    toast.addEventListener('mouseenter', Swal.stopTimer)
    toast.addEventListener('mouseleave', Swal.resumeTimer)
  } */
})

export default{
    name: 'SetPage',
    components: {

    },
    data(){
        return{
            files,
            Wsetpage: 'main',
            firstJSON,
            firJSON,
            fileForWord,
            fileNameFor,
            randNum,
            wordList,
            wordMeanings,
            wordWords,
            probCount,
            clickCount,
            correctAnswer,
            arr: ['시작하기','시작하기','시작하기'],
            rdwords: ['시작하기','시작하기','시작하기'],
            pages: ['중1 비상 8단원', '중1 비상 9단원', '중1 비상 10단원'],
        };
    },
    methods:{
        changePageWords: function(fileName){
            console.log(typeof firJSON);
            console.log(firJSON[0].word);
            console.log(firJSON.length);
            console.log(fileName);
            if(this.Wsetpage == 'main'){
                this.Wsetpage = 'words';
            } else if(this.Wsetpage == 'words'){
                this.Wsetpage = 'main';
            }
            if(fileName == '중1 비상 8단원'){
                this.fileForWord = this.firstJSON;
                console.log(fileForWord.length);
            }
            this.fileNameFor = fileName;
            if(this.Wsetpage == 'words'){
                Swal.fire({
                    icon: 'info',
                    title: '개발중인 페이지입니다.',
                    text: '메인 메뉴로 돌아가려면 새로고침하십시오.',
                    showCancelButton: false,
                    confirmButtonText: '예', 
                    confirmButtonColor: '#429f50',
                })
            }
        },
        toAm(){
            Toast.fire({
                    icon:'info',
                    title:'정답 개수를 보여주는 기능은 개발중입니다!',
                    text:'양해해주세요',
                });
            this.Wsetpage = 'word1';
        },
        endFunc(a){
                Swal.fire({
                    icon:'success',
                    title:'암기 학습을 완료했습니다!',
                    text: a + '개 맞추셨습니다! 메인 메뉴로 돌아가기 위해 새로고침을 눌러 주세요',
                    showCancelButton:false,
                    showConfirmButton:false,
                    allowOutsideClick:false
                });
        },
        changeWordProbT2(ans){
            let arr2 = new Array();
            let i, a, count, b;
            let set;
            if(this.clickCount != 0){
                if(this.probCount == (this.fileForWord.length-1)){
                    this.endFunc(this.correctAnswer);
                } else{
                    this.probCount = this.probCount + 1;
                }
            } else if(this.clickCount === 0){
                this.clickCount = 1;
                this.probCount = 0;
            }
            a = Math.floor(Math.random() * this.fileForWord.length);
            b = Math.floor(Math.random() * this.fileForWord.length);
            console.log(a)
            while(a == (this.probCount)){
                a = Math.floor(Math.random() * fileForWord.length);
                console.log(`re: ${a} `);
            }
            while(b == (this.probCount) || b == a){
                b = Math.floor(Math.random() * fileForWord.length);
                console.log(`re: ${b} `);
            }
            this.arr.splice(0,this.arr.length);
            this.arr.push(fileForWord[this.probCount].meaning);
            this.arr.push(fileForWord[a].meaning);
            this.arr.push(fileForWord[b].meaning);
            this.arr.sort(() => Math.random() - 0.5);
            console.log(this.arr);
            console.log(`probCount : ${probCount} `);
            if(ans == arr.indexOf(fileForWord[this.probCount].meaning)){
                this.correctAnswer = this.correctAnswer + 1;
                console.log("정답!");
            }
            console.log(this.correctAnswer);
        }

    },
}
</script>
